.SearchBar{
    padding-bottom: 40px;
    display:flex;
    flex-flow: row;
    padding-top: 24px;
}
.Input{
    width: 80%;
    height:36px;
    text-align: center;
    font-family: cursive;
    color: bisque;
    border-radius: 5px;
    border:2px solid #225566;
    margin-right: 16px;
    margin-left:8%;
    background-color: rgb(49, 65, 14);
}
.Button{
    width:20%;
    border-radius: 5px;
    border:2px solid #225566;
    background-color: rgb(49, 65, 14);
    color: bisque;
    height: 42px;
    margin-right: 8%;
    font-family: cursive;
}
.Background {
    padding-bottom: 40px;
}